import React, { useState } from "react";
import { Post } from "./Post";
import Picker from "emoji-picker-react";

export const Card = () => {
  const [text, setText] = useState("");
  const [img, setImg] = useState("");
  const [modal, setmodal] = useState(false);
  const [selectedImage, setSelectedImage] = useState("");
  const [imgurl, setImgurl] = useState([]);
  const [imgurl1, setImgurl1] = useState([]);
  const [arr, setArr] = useState([]);
  const handleChange = (e) => {
    setText(e.target.value);
  };

  const displayModal = (e) => {
    e.preventDefault();
    setmodal(!modal);
  };
  const submitHandler = (e) => {
    e.preventDefault();
    const data = { value: text, image: selectedImage };
    setArr([...arr, data]);
    setText("");
    setSelectedImage("");
  };

  const imageHandler = async (e) => {
    e.preventDefault();
    setImg(e.target.value);
    let imgage =
      await fetch(`https://api.giphy.com/v1/gifs/search?api_key=VsCimMLxyvCNn3HEMQfys7kwMQPf190F&q=${img}&limit=25&offset=0&rating=g&lang=en
    `);
    let datas = await imgage.json();
    let imgage1 =
      await fetch(`https://api.giphy.com/v1/gifs/trending?api_key=VsCimMLxyvCNn3HEMQfys7kwMQPf190F&limit=25&rating=g
    `);
    let datas1 = await imgage1.json();
    console.log(datas);
    console.log(selectedImage);
    setImgurl(datas.data.map((p) => p.images.original.url));
    setImgurl1(datas1.data.map((p) => p.images.original.url));
  };

  return (
    <>
      <div className="container">
        <div className="col-md-10 mx-auto">
          <div class="card text-center mt-4">
            <div class="card-body" id="header">
              <p>
                <span>
                  <i class="fas fa-pencil-alt"></i>{" "}
                </span>
                Compose Post
              </p>{" "}
              <span style={{ color: "lightgray" }}>|</span>
              <p>
                <span>
                  <i class="fas fa-camera-retro"></i>{" "}
                </span>
                Photo/Video Album
              </p>{" "}
              <span style={{ color: "lightgray" }}>|</span>
              <span>
                <i class="fas fa-video ml-2"></i>{" "}
              </span>
              <p>Live Video</p>
              <p class="ml-auto">X</p>
            </div>
            <div class="card-body">
              <img
                className="float-left"
                src="data:image/png;base64,"
              ></img>
              <input
                id="body"
                type="text"
                class="form-control"
                value={text}
                placeholder="Write something here..."
                onChange={(e) => handleChange(e)}
              ></input>
            </div>
            {selectedImage ? (
              <div class="table-wrapper-scroll-x mx-custom-scrollbar">
                <table class="table mb-0">
                  <tbody>
                    <img
                      class="float-left"
                      src={selectedImage}
                      style={{ height: "100px", width: "80px" }}
                    ></img>
                  </tbody>
                </table>
              </div>
            ) : null}
            <div className="row mt-4">
              <div className="col-md-6 mt-2">
                <button type="button" class="btn btn-light">
                  <span>
                    <i
                      class="fa fa-users"
                      aria-hidden="false"
                      style={{ color: "#57a3de" }}
                    ></i>
                  </span>{" "}
                  Tag Friends
                </button>
              </div>
              <div className="col-md-6 mt-2">
                <button type="button" class="btn btn-light">
                  <span>
                    <i
                      class="fa fa-map-marker pr-1"
                      style={{ color: "#db442a" }}
                    ></i>
                  </span>
                  {"  "}
                  Check in
                </button>
              </div>
            </div>
            <div className="row mt-2">
              <div className="col-md-6 mt-2">
                <button
                  onClick={displayModal}
                  type="button"
                  class="btn btn-light"
                >
                  <span id="gif">GIF</span> GIF
                </button>
                <div class="hide">
                  {modal ? (
                    <input
                      type="text"
                      id="body"
                      class="form-control"
                      value={img}
                      onChange={imageHandler}
                    ></input>
                  ) : null}

                  <div class="table-wrapper-scroll-x mx-custom-scrollbar">
                    <table class="table table-bordered table-striped mb-0">
                      <tbody>
                        {img
                          ? imgurl.map((images) => {
                              return (
                                <tr>
                                  <td>
                                    <img
                                      onClick={() => {
                                        setSelectedImage(images);
                                      }}
                                      src={images}
                                      style={{ width: "100px", height: "auto" }}
                                    ></img>
                                  </td>
                                </tr>
                              );
                            })
                          : modal
                          ? imgurl1.map((images) => {
                              return (
                                <tr>
                                  <td>
                                    <a>
                                      <img
                                        onClick={() => {
                                          setSelectedImage(images);
                                        }}
                                        src={images}
                                        style={{
                                          width: "100px",
                                          height: "auto",
                                        }}
                                      ></img>
                                    </a>
                                  </td>
                                </tr>
                              );
                            })
                          : null}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
              <div className="col-md-6 mt-2">
                <button type="button" class="btn btn-light">
                  <span>
                    <i
                      class="fa fa-calendar pr-2"
                      style={{ color: "#ed9687" }}
                    ></i>
                  </span>{" "}
                  Tag Event
                </button>
              </div>
            </div>
            <div class="card-footer mt-4" id="header">
              <div className="ml-auto">
                <button
                  type="button"
                  class="btn "
                  style={{ border: "1px solid black" }}
                >
                  <span>
                    <i class="fa fa-lock pr-2" aria-hidden="false"></i>
                  </span>
                  Only me
                </button>{" "}
                <button
                  type="submit"
                  class="btn btn"
                  style={{ backgroundColor: "#4267B2", color: "white" }}
                  onClick={submitHandler}
                >
                  Post
                </button>
              </div>
            </div>
          </div>
        </div>
        {arr !== []
          ? arr.map((p) => {
              return <Post post={p} />;
            })
          : null}
      </div>
    </>
  );
};
